import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import { Form, Input, Button, Alert } from "antd";
import { connect } from "react-redux";
import { register } from "../../actions/auth";
import PropTypes from "prop-types";
import { updateTitle } from "../common/UpdateTitle";

class Signup extends Component {
  state = {
    has_login_error: false,
    login_error_msg: "",
  };
  componentDidMount(){
    updateTitle({
      title:"Sign Up"
    })
  }
  componentDidUpdate(oldProps) {
    const { error } = this.props;

    if (error !== oldProps.error) {
      console.log(error);

      if (error.msg.detail) {
        this.setState({
          ...this.state,
          has_login_error: true,
          login_error_msg: error.msg.detail,
        });
      }
      if (error.msg.username) {
        this.setState({
          has_login_error: true,
          login_error_msg: error.msg.username.join(),
        });
      }
      if (error.msg.email) {
        this.setState({
          has_login_error: true,
          login_error_msg: error.msg.email.join(),
        });
      }

      if (error.msg.phone) {
        this.setState({
          has_login_error: true,
          login_error_msg: error.msg.phone.join(),
        });
      }
    }
  }
  static propTypes = {
    register: PropTypes.func.isRequired,
    isAuthenticated: PropTypes.bool,
  };
  onSubmit = (e) => {
    this.setState({
      has_login_error: false,
      login_error_msg: "",
    });
    console.log("Submitted", e);
    this.props.register(
      e.first_name,
      e.last_name,
      e.username,
      e.email,
      e.password,
      e.phone
    );
  };
  render() {
    const { from } = this.props.location.state || { from: { pathname: "/" } };
    if (this.props.isAuthenticated) {
      return <Redirect to={from} />;
    }
    let { login_error_msg, has_login_error } = this.state;
    return (
      <div className="mdk-header-layout__content page-content ">
        <div className="container pt-32pt pt-sm-64pt pb-32pt">
          <div className="page__container page-section mt-3 mb-1 pb-0">
            <h1 className="h2 text-center">Registration</h1>
          </div>
          <div className="page__container">
            <Form
              onFinish={this.onSubmit}
              className="col-md-6 mx-auto"
              scrollToFirstError={true}
            >
              {has_login_error ? (
                <Alert
                  className="my-3"
                  message={login_error_msg}
                  type="error"
                  showIcon
                  closable
                />
              ) : null}
              <Form.Item
                label="First Name"
                name="first_name"
                className="form-group"
              >
                <Input className="form-control" placeholder="Your First Name" />
              </Form.Item>
              <Form.Item
                className="form-group"
                label="Last Name"
                name="last_name"
              >
                <Input className="form-control" placeholder="Your Last Name" />
              </Form.Item>
              <Form.Item
                className="form-group"
                label="Username"
                name="username"
                rules={[
                  {
                    required: true,
                    message: "Please input your username!",
                  },
                ]}
              >
                <Input className="form-control" placeholder="Username" />
              </Form.Item>
              <Form.Item
                className="form-group"
                label="Phone Number"
                name="phone"
                rules={[
                  {
                    required: true,
                    message: "Please input your Phone Number!",
                  },
                ]}
              >
                <Input
                  type="number"
                  className="form-control"
                  placeholder="Phone Number"
                />
              </Form.Item>
              <Form.Item
                className="form-group"
                label="Email"
                name="email"
                rules={[
                  {
                    type: "email",
                    message: "The input is not valid E-mail!",
                  },
                ]}
              >
                <Input
                  type="email"
                  className="form-control"
                  placeholder="Your email address(optional)"
                />
              </Form.Item>
              <Form.Item
                className="form-group"
                label="Passowrd"
                name="password"
                hasFeedback
                rules={[
                  {
                    required: true,
                    message: "Please input new password!",
                  },
                  {
                    min: 8,
                    message: "Password should be 8 charecters or more",
                  },
                ]}
              >
                <Input.Password
                  id="password"
                  className=""
                  placeholder="New Password"
                  autoComplete="on"
                />
              </Form.Item>
              <Form.Item
                className="form-group"
                label="Confirm Password"
                name="password2"
                dependencies={["password"]}
                hasFeedback
                rules={[
                  {
                    required: true,
                    message: "Please confirm your password!",
                  },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue("password") === value) {
                        return Promise.resolve();
                      }

                      return Promise.reject(
                        new Error(
                          "The two passwords that you entered do not match!"
                        )
                      );
                    },
                  }),
                ]}
              >
                <Input.Password
                  autoComplete="on"
                  className=""
                  placeholder="Please Retype Your Password"
                />
              </Form.Item>
              <p className="">
                <Link to="/login" className="">
                  Already a member?
                </Link>
              </p>
              <Form.Item className="text-right">
                <Button htmlType="submit" className="btn btn-primary">
                  Signup
                </Button>
              </Form.Item>
            </Form>
          </div>
        </div>
        <div className="bg-body pb-32pt pb-md-64pt text-center"></div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  error: state.errors,
});

export default connect(mapStateToProps, { register })(Signup);
